<template>
      <div class="zongji1">
        <div class="zongji2">
          <p>总机号码管理</p>
        </div>
        <div class="zongji3">
          <ul>
            <li>
              所有属性&emsp;&emsp;<span class="el-icon-caret-bottom"></span>
            </li>
          </ul>
          <input
            type="text"
            class="putt"
            placeholder="请输入总机号码进行模糊搜索"
            v-model="search"
          />
          <button @click="sear()" class="but">
            <i class="el-icon-search"></i>
          </button>
        </div>
        <div class="zongji4">
          <el-table :data="tableData" style="width: 100%">
            <el-table-column prop="date" label="总机号码"> </el-table-column>
            <el-table-column prop="name" label="号码状态"> </el-table-column>
            <el-table-column prop="yn" label="号码属性"> </el-table-column>
            <el-table-column prop="" label="归属组/坐席"> </el-table-column>
            <el-table-column prop="" label="组名称"> </el-table-column>
            <el-table-column prop="" label="坐席名称"> </el-table-column>
            <el-table-column prop="" label="呼入IVR"> </el-table-column>
            <el-table-column prop="" label="开启状态">
              <template slot-scope="scope">
                <el-switch
                  v-model="scope.row.name"
                  active-value="正常"
                  inactive-value="异常"
                  @click.native="fun(scope.$index, scope.row)"
                >
                </el-switch>
              </template>
            </el-table-column>
            <el-table-column prop="data" label="最后编辑时间" width="180">
              <!-- {{date}} -->
            </el-table-column>
            <el-table-column prop="address" label="操作"
              ><i class="el-icon-setting" style="font-size: 18px"></i
            ></el-table-column>
          </el-table>
        </div>
        <div class="zongji5">
          <p style="float: left; margin-top: 10px; marhin-left: 30px">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage4"
              :page-sizes="[100, 200, 300, 400]"
              :page-size="100"
              layout="total, sizes"
              :total="400"
            >
            </el-pagination>
          </p>
          <p style="float: right; margin-top: 10px; marhin-right: 30px">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage4"
              :page-sizes="[100, 200, 300, 400]"
              background
              :page-size="100"
              layout="prev, pager, next, jumper"
              :total="400"
            >
            </el-pagination>
          </p>
        </div>
      </div>
</template>

<script>
import "./zongji.css";
var arr = [];
export default {
  data() {
    return {
      currentPage4: 4,
      search: "",
      date: "",
      tableData: [
        {
          date: "7659893732",
          name: "正常",
          yn: "公共",
          data: "2022-4-1 16:49:42",
        },
        {
          date: "0123675548",
          name: "正常",
          yn: "公共",
          data: "2022-4-1 16:49:12",
        },
        {
          date: "1541234557",
          name: "正常",
          yn: "公共",
          data: "2022-4-1 16:53:10",
        },
      ],
    };
  },
  methods: {
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    sear() {
      this.tableData = arr.filter((item) => {
        console.log(item);
        if (item.date.includes(this.search)) {
          return item;
        }
      });
    },
    array() {
      arr = [...this.tableData];
    },
    fun(index, row) {
      console.log(index, row);
      if (index == 0) {
        let data = new Date(),
          nian = data.getFullYear(),
          yue = data.getMonth() + 1,
          ri = data.getDate(),
          shi = data.getHours(),
          fen = data.getMinutes(),
          miao = data.getSeconds();
        this.tableData[0].data =
          nian + "-" + yue + "-" + ri + " " + shi + ":" + fen + ":" + miao;
      } else if (index == 1) {
        let data = new Date(),
          nian = data.getFullYear(),
          yue = data.getMonth() + 1,
          ri = data.getDate(),
          shi = data.getHours(),
          fen = data.getMinutes(),
          miao = data.getSeconds();
        this.tableData[1].data =
          nian + "-" + yue + "-" + ri + " " + shi + ":" + fen + ":" + miao;
      } else if (index == 2) {
        let data = new Date(),
          nian = data.getFullYear(),
          yue = data.getMonth() + 1,
          ri = data.getDate(),
          shi = data.getHours(),
          fen = data.getMinutes(),
          miao = data.getSeconds();
        this.tableData[2].data =
          nian + "-" + yue + "-" + ri + " " + shi + ":" + fen + ":" + miao;
      }
    },
  },
  mounted() {
    this.array();
  },
  components: {},
};
</script>